*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}
body{
  width: 100%;
  height: 100vh;
  background-color: white;

}
.search h1{
  text-align: center;
  color: white;
  margin: auto 20px;
  position: absolute;
  left: 30px;
}
.search{
      width: 100%;
      height: 70px;
      background-color: black;
      margin-bottom: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      position:sticky;
      top: 0;
}
.search input{
  height: 2rem;
  width: 20rem;
  margin-right: 1rem;
  border-radius: 5px;
  border: none;
  padding-left: 5px;
}


.search button {
align-items: center;
background-image: linear-gradient(144deg,#AF40FF, #5B42F3 50%,#00DDEB);
color: white;
width: 130px;
height: 33px;
border: none;
border-radius: 5px;
cursor: pointer;
letter-spacing: 1px;
}

/* Design of  MovieCard*/

.container{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.container .movie{

  margin: 10px;
  padding: 10px;
  background-color: white;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 5px;

}
